<template>
  <view class="reMark_wrap" :class="isCusDet ? 'noBorber' : ''">
    <view class="reMark">
      <text>{{ textName }}</text>
      <view class="reTextarea">
        <textarea
          v-model="areaValue"
          :placeholder="'请填写' + textName"
          @blur="getValue"
        />
      </view>
    </view>
  </view>
</template>

<script>
export default {
  props: ["textName", "remarkValue", "isCusDet"],
  data() {
    return {
      areaValue: "", // 输入框内容
    };
  },
  created() {
    this.areaValue = this.remarkValue ? this.remarkValue : this.areaValue;
  },
  methods: {
    getValue() {
      this.$emit("getValue", this.areaValue);
    },
  },
};
</script>

<style lang="scss">
.noBorber {
  border-bottom: none !important;
}
.reMark_wrap {
  border-top: 20rpx solid #f6f6f6;
  border-bottom: 20rpx solid #f6f6f6;
  .reMark {
    padding: 30rpx;
    .reTextarea {
      margin-top: 25rpx;
      width: 690rpx;
      height: 200rpx;
      border-radius: 8rpx;
      border: 1rpx solid #eee;
      padding: 20rpx;
      textarea {
        width: 650rpx;
        height: 200rpx;
      }
    }
  }
}
</style>
